import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import './CategoryView.css';

function CategoryView() {
  const { category } = useParams();
  const [pdfs, setPdfs] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  
  const categoryTitles = {
    salud: 'Salud',
    banca: 'Banca',
    telco: 'Telecomunicaciones'
  };
  
  const categoryIcons = {
    salud: '',
    banca: '',
    telco: ''
  };
  
  useEffect(() => {
    const loadPdfs = () => {
      setLoading(true);
      
      try {
        const storedPdfs = JSON.parse(localStorage.getItem('pdfs') || '[]');
        const filteredPdfs = storedPdfs.filter(pdf => pdf.category === category);
        setPdfs(filteredPdfs);
      } catch (err) {
        console.error('Error loading PDFs:', err);
      } finally {
        setLoading(false);
      }
    };
    
    loadPdfs();
  }, [category]);
  
  const filteredPdfs = pdfs.filter(pdf => 
    pdf.name.toLowerCase().includes(searchTerm.toLowerCase())
  );
  
  return (
    <div className="category-view-container">
      <div className="category-header">
        <div className="category-title">
          <span className="category-icon">{categoryIcons[category] || ''}</span>
          <h2>Documentos de {categoryTitles[category] || category}</h2>
        </div>
        
        <div className="search-container">
          <input
            type="text"
            placeholder="Buscar documentos..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="search-input"
          />
        </div>
      </div>
      
      {loading ? (
        <div className="loading">Cargando documentos...</div>
      ) : (
        <>
          {filteredPdfs.length === 0 ? (
            <div className="no-pdfs">
              <p>No hay documentos en esta categora.</p>
              <Link to="/upload" className="upload-link">Subir un PDF</Link>
            </div>
          ) : (
            <div className="pdf-grid">
              {filteredPdfs.map(pdf => (
                <div key={pdf.id} className="pdf-card">
                  <div className="pdf-card-icon"></div>
                  <div className="pdf-card-content">
                    <h3 className="pdf-card-title">{pdf.name}</h3>
                    <p className="pdf-card-date">
                      {new Date(pdf.dateAdded).toLocaleDateString()}
                    </p>
                    <p className="pdf-card-size">{pdf.size}</p>
                  </div>
                  <div className="pdf-card-actions">
                    <Link to={`/view/${pdf.id}`} className="pdf-action-button view-button">
                      Ver
                    </Link>
                    <Link to={`/qr/${pdf.id}`} className="pdf-action-button qr-button">
                      QR
                    </Link>
                  </div>
                </div>
              ))}
            </div>
          )}
        </>
      )}
      
      <div className="category-actions">
        <Link to="/upload" className="category-action-button upload-button">
          Subir Nuevo PDF
        </Link>
        <Link to="/" className="category-action-button home-button">
          Volver al Inicio
        </Link>
      </div>
    </div>
  );
}

export default CategoryView;