import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import { Document, Page, pdfjs } from 'react-pdf';
import './PDFViewer.css';

// Set up the worker for PDF.js
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

function PDFViewer() {
  const { id } = useParams();
  const [pdf, setPdf] = useState(null);
  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  
  useEffect(() => {
    const loadPdf = () => {
      setLoading(true);
      
      try {
        const storedPdfs = JSON.parse(localStorage.getItem('pdfs') || '[]');
        const foundPdf = storedPdfs.find(item => item.id === id);
        
        if (foundPdf) {
          setPdf(foundPdf);
          setError('');
        } else {
          setError('PDF no encontrado. Es posible que haya sido eliminado o que el enlace sea incorrecto.');
        }
      } catch (err) {
        setError('Error al cargar el PDF. Intntelo de nuevo.');
      } finally {
        setLoading(false);
      }
    };
    
    loadPdf();
  }, [id]);
  
  const onDocumentLoadSuccess = ({ numPages }) => {
    setNumPages(numPages);
  };
  
  const changePage = (offset) => {
    setPageNumber(prevPageNumber => {
      const newPageNumber = prevPageNumber + offset;
      return Math.min(Math.max(1, newPageNumber), numPages);
    });
  };
  
  const previousPage = () => changePage(-1);
  const nextPage = () => changePage(1);
  
  if (loading) {
    return <div className="loading">Cargando PDF...</div>;
  }
  
  if (error) {
    return <div className="error-container">{error}</div>;
  }
  
  return (
    <div className="pdf-viewer-container">
      <div className="pdf-header">
        <h2>{pdf.name}</h2>
        <div className="pdf-info">
          <p>Categora: {pdf.category}</p>
          <p>Tamao: {pdf.size}</p>
          <p>Fecha: {new Date(pdf.dateAdded).toLocaleDateString()}</p>
        </div>
      </div>
      
      <div className="pdf-controls">
        <button 
          onClick={previousPage} 
          disabled={pageNumber <= 1}
          className="control-button"
        >
          Anterior
        </button>
        <span className="page-info">
          Pgina {pageNumber} de {numPages || '--'}
        </span>
        <button 
          onClick={nextPage} 
          disabled={pageNumber >= numPages}
          className="control-button"
        >
          Siguiente
        </button>
      </div>
      
      <div className="pdf-document-container">
        <Document
          file={pdf.data}
          onLoadSuccess={onDocumentLoadSuccess}
          className="pdf-document"
        >
          <Page 
            pageNumber={pageNumber} 
            className="pdf-page"
            renderTextLayer={false}
            renderAnnotationLayer={false}
          />
        </Document>
      </div>
      
      <div className="pdf-actions">
        <Link to={`/qr/${id}`} className="action-link qr-link">
          Generar Cdigo QR
        </Link>
        <a 
          href={pdf.data} 
          download={`${pdf.name}.pdf`}
          className="action-link download-link"
        >
          Descargar PDF
        </a>
        <Link to="/" className="action-link back-link">
          Volver al Inicio
        </Link>
      </div>
    </div>
  );
}

export default PDFViewer;