import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './Home.css';

function Home() {
  const [recentPdfs, setRecentPdfs] = useState([]);
  
  useEffect(() => {
    // Load PDFs from localStorage
    const loadPdfs = () => {
      const storedPdfs = localStorage.getItem('pdfs');
      if (storedPdfs) {
        const parsedPdfs = JSON.parse(storedPdfs);
        // Sort by date and get the 5 most recent
        const sortedPdfs = parsedPdfs.sort((a, b) => 
          new Date(b.dateAdded) - new Date(a.dateAdded)
        ).slice(0, 5);
        
        setRecentPdfs(sortedPdfs);
      }
    };
    
    loadPdfs();
  }, []);
  
  return (
    <div className="home-container">
      <section className="hero-section">
        <h2>Bienvenido al Gestor de PDFs de NTT DATA</h2>
        <p>Gestione, visualice y comparta sus documentos PDF de manera sencilla</p>
        <Link to="/upload" className="cta-button">Subir un PDF</Link>
      </section>
      
      <section className="categories-section">
        <h3>Categoras</h3>
        <div className="category-cards">
          <Link to="/category/salud" className="category-card">
            <div className="category-icon"></div>
            <h4>Salud</h4>
            <p>Documentos relacionados con el sector salud</p>
          </Link>
          
          <Link to="/category/banca" className="category-card">
            <div className="category-icon"></div>
            <h4>Banca</h4>
            <p>Documentos relacionados con el sector bancario</p>
          </Link>
          
          <Link to="/category/telco" className="category-card">
            <div className="category-icon"></div>
            <h4>Telecomunicaciones</h4>
            <p>Documentos relacionados con telecomunicaciones</p>
          </Link>
        </div>
      </section>
      
      {recentPdfs.length > 0 && (
        <section className="recent-section">
          <h3>Documentos Recientes</h3>
          <div className="pdf-list">
            {recentPdfs.map(pdf => (
              <div key={pdf.id} className="pdf-item">
                <div className="pdf-icon"></div>
                <div className="pdf-details">
                  <h4>{pdf.name}</h4>
                  <p>Categora: {pdf.category}</p>
                  <p>Aadido: {new Date(pdf.dateAdded).toLocaleDateString()}</p>
                </div>
                <div className="pdf-actions">
                  <Link to={`/view/${pdf.id}`} className="action-button view-button">Ver</Link>
                  <Link to={`/qr/${pdf.id}`} className="action-button qr-button">QR</Link>
                </div>
              </div>
            ))}
          </div>
        </section>
      )}
    </div>
  );
}

export default Home;